*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  @include fonts();

  display: flex;
  margin: 0;
  flex-direction: column;
  min-width: 320px;
  background-color: $basic-white;
  overflow-x: hidden;

  @media (min-width: $tablet-width) {
    font-size: $main-font-size;
    line-height: $main-line-height;
    background-color: $basic-white;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
