  .main-nav {
    justify-content: start;
  }
}

.main-nav__logo {
  align-self: center;
}

.main-nav__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: block;
    position: relative;
    z-index: 5;
  }
  .main-nav__toggle::before {
    position: absolute;
    top: calc(50% - 8px);
    left: calc(50% - 8px);
    width: 16px;
    height: 1.5px;
    background-color: #333333;
    box-shadow: 0 5px 0 0 #333333;
    content: "";
    transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
  }
  .main-nav__toggle::after {
    position: absolute;
    top: calc(50% + 2px);
    left: calc(50% - 8px);
    width: 16px;
    height: 1.5px;
    background-color: #333333;
    content: "";
    transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
  }
}

.main-nav__wrapper {
  position: absolute;
  top: -300px;
  left: 0;
  display: flex;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
    display: flex;
    width: auto;
    flex-direction: row;
  }
}

.main-nav__toggle--noJS {
  display: none;
}
.main-nav__toggle--noJS + .main-nav__wrapper {
  display: flex;
  width: 280px;
}

.main-nav__toggle--close::before {
  position: absolute;
  top: calc(50% - 8px);
  left: calc(50% - 8px);
  width: 16px;
  height: 1.5px;
  background-color: #333333;
  box-shadow: 0 5px 0 0 #333333;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}
.main-nav__toggle--close::after {
  position: absolute;
  top: calc(50% + 2px);
  left: calc(50% - 8px);
  width: 16px;
  height: 1.5px;
  background-color: #333333;
  content: "";
  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
}

.main-nav__toggle--open::before {
  box-shadow: none;
  top: 27px;
  transform: rotate(45deg);
}
.main-nav__toggle--open::after {
  top: 27px;
  left: 21px;
  transform: rotate(-45deg);
}
.main-nav__toggle--open + .main-nav__wrapper {
  top: 60px;
}

