  transition: all 0.4s cubic-bezier(0.1, 0.82, 0.76, 0.96);
  cursor: pointer;
}

.pagination__link:not(.pagination__link--current):hover {
  background-color: #9070ec;
  color: #ffffff;
}

.pagination__link:not(.pagination__link--current):active {
  background-color: #593cac;
  color: #ffffff;
}

