.pagination__arrow {
  display: block;
  width: 40px;
  height: 40px;
  padding: 8px 20px;
  position: relative;
}
.pagination__arrow::after {
  position: absolute;
  content: "";
  top: 21px;
  left: 16px;
  width: 6px;
  height: 1.5px;
  background-color: #7859cf;
  transform: rotate(45deg);
}
.pagination__arrow::before {
  position: absolute;
  content: "";
  top: 17px;
  left: 17px;
  width: 5px;
  height: 1.5px;
  background-color: #7859cf;
  transform: rotate(-45deg);
}
