    width: auto;
    height: auto;
  }
  .pagination__arrow::after, .pagination__arrow::before {
    display: none;
  }
}

.pagination__arrow:hover::after, .pagination__arrow:hover::before,
.pagination__arrow:active::after,
.pagination__arrow:active::before {
  background-color: #ffffff;
}

